@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*:focus {
	outline: none !important;
}

html, body {
    width: 100%;
    height: 100%;
    font-family: 'Roboto', sans-serif;
}

body {
	background: #F6FBFD;
}

a { 
	text-decoration: none; 
	color: #000000;
}

h1 {
	font-size: 38px;
	font-weight: 400;
	text-transform: uppercase;
	text-align: center;
}

h2 {
	font-size: 30px;
	font-weight: 300;
	text-align: center;
}

h3 {
	text-align: center;
}

h4 {
	font-size: 24px;
	font-weight: 400;
}

button {
	padding: 1% 1.5%;
	margin: 1%;
	float: right;
	background: none;
	background-image: none;
	border-radius: 5%;
    border: 2px solid transparent;
    text-shadow: 0px 0px 0px transparent;
   	box-shadow: none;
   	font-size: 16px;
}

button:hover {
	cursor: pointer;
}

.navigation {
	padding: 0%;
	margin: 0;
	width: 100%;
}

.hero {
	padding: 22% 0%;
	margin: 0;
	width: 100%;
	background-image: url(/landing-page/static/media/shutterstock.da0eeb2c.png); 
	background-repeat: no-repeat;
	background-size: cover;
}

.container {
	padding: 2% 0%;
	margin: 0;
	width: 100%;
	background-image: url(data:image/png;base64,); 
	background-repeat: no-repeat;
	background-size: cover;
	display: flex;
	flex-direction: row;
}

.photo {
	flex: 1 1;
}

.agentphoto {
	width: 70%;
	border-radius: 50%;
	float: right;
	padding: 10% 5%;
}

.contact {
	flex: 3 1;
	padding: 0 5%;
}

.icon {
	width: 5%;
	vertical-align: middle;
	padding: 0 20px 0 0;
}

@media screen and (max-width: 800px) {
  .agentphoto {
  	display: block;
  	padding: 0;
  	margin: 20px auto 0;
	width: 30%;
	float: none;
  }
  .container {
  	flex-direction: column;
  }
}

